import { Box, makeStyles, Typography, FormHelperText } from "@material-ui/core";
import Button from "../../Components/Button";
import React, { useState, useEffect } from "react";
import Navbar from "../../Components/Navbar";
import Footer from "../../Components/Footer/Footer";
import About from "../../Components/AboutSection";
import MobileDrawer from "../../Components/MobileDrawer";
const EmailUnsubscribe = () => {
  const styles = useStyles();
  const [mobileDrawer, setMobileDrawer] = useState(false);
  const [emailAddress, setEmailAddress] = useState("");
  const [emailAddressError, setEmailAddressError] = useState(false);

  useEffect(() => {
    setEmailAddressError(false);
  }, [emailAddress]);

  const submitEmailAddress = async () => {
    if (emailAddress === "" || !emailAddress.includes("@")) {
      setEmailAddressError(true);
      return;
    }
    const res = fetch(
      "https://neend-app-default-rtdb.firebaseio.com/unsubscribe_emails.json",
      {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          emailAddress,
        }),
      }
    );
    if (res) {
      alert("Thanks for submition");
      setEmailAddress("");
    } else {
      alert("Your email not captured try again!");
    }
  };

  return (
    <Box className={styles.emailUnsubscribe} component="div" display="flex">
      <Navbar />
      <MobileDrawer
        open={mobileDrawer}
        handleClose={() => setMobileDrawer(false)}
      />
      <Typography className={styles.unsubscribeText}>
        Unsubscribe from our mailing list
      </Typography>
      <input
        value={emailAddress}
        className={styles.inputBox}
        placeholder="Email Address"
        onChange={(event) => setEmailAddress(event.target.value)}
      />
      <FormHelperText className={styles.helperText} error={emailAddressError}>
        Plz enter valid email!
      </FormHelperText>
      <Button
        titleColor="#fff"
        backgroundColor="#937DE2"
        variant="contained"
        title="Submit"
        width={202}
        height={50}
        radius={40}
        textSize={16}
        borderWidth={2}
        click={submitEmailAddress}
      />
      <About />
      <Footer />
    </Box>
  );
};

export default EmailUnsubscribe;
const useStyles = makeStyles((theme) => ({
  emailUnsubscribe: {
    justifyContent: "center",
    alignItems: "center",
    flexDirection: "column",
  },
  unsubscribeText: {
    marginTop: 160,
    color: "#fff",
    fontSize: 24,
    fontFamily: "Open Sans",
    [theme.breakpoints.down("sm")]: {
      fontSize: 14,
      marginTop: 100,
    },
  },
  inputBox: {
    width: 340,
    height: 48,
    marginTop: 32,
    borderRadius: 8,
    paddingInline: 12,
    fontSize: 14,
    fontFamily: "Open Sans",
  },
  helperText: {
    fontSize: 14,
    width: 330,
    marginBottom: 32,
    color: "#03164C",
  },
}));
