import React, { Suspense, useState } from "react";
import About from "../../Components/AboutSection";
import AppFeature from "../../Components/AppFeature";
import CenterText from "../../Components/Content";
import Footer from "../../Components/Footer/Footer.jsx";
import Navbar from "../../Components/Navbar";
import ProductSection from "../../Components/Products";
import SectionTitle from "../../Components/SectionTitle";
import CarouselScreen from "../../Components/ReviewSection";
import "../../i18n";
import { useWindowDimensions } from "../../utils/design.utils.js";
import MobileDrawer from "../../Components/MobileDrawer";

const Home = () => {
  useWindowDimensions();
  const [mobileDrawer, setMobileDrawer] = useState(false);

  return (
    <>
      <Suspense fallback={null}>
        <Navbar onClick={() => setMobileDrawer(true)} />
        <MobileDrawer
          open={mobileDrawer}
          handleClose={() => setMobileDrawer(false)}
        />
        <CenterText />
        <SectionTitle title="APP FEATURES" />
        <AppFeature />
        <SectionTitle title="PRODUCTS" />
        <ProductSection />
        <SectionTitle title="REVIEWS" />
        <CarouselScreen />
        <About />
        <Footer />
      </Suspense>
    </>
  );
};

export default Home;
